/* Navigation Bar */
.navbar {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 70px;
    background-color: rgba(#333, .64);
    z-index: 9999;
    text-align: right;
    @media #{$mediaExSmall} {
        padding-top: 0;
    }
    @media #{$mediaSmall} {
        padding-top: 0;
    }
    @media #{$mediaMedium} {
        padding-top: 0;
    }
    .navbar-collapse {
        .navbar-nav {        
            @media #{$mediaExSmall} {
                background-color: rgba(#333, .74);
            }
            @media #{$mediaSmall} {
                background-color: rgba(#333, .74);
            }
            @media #{$mediaMedium} {
                background-color: rgba(#333, .74);
            }
            .nav-link {
                color: #f0f0f0;
                font-size: 15px;
                font-weight: 600;
                padding: 13px;
                transition: .2s;
                text-shadow: 0 0 3px #000;
                @media #{$mediaExSmall} {
                    font-size: 13px;
                }
                @media #{$mediaSmall} {
                    font-size: 13px;
                }
                @media #{$mediaMedium} {
                    font-size: 14px;
                }
                &:hover {
                    color: $mainColor
                }
            }
        }
    }
}